import React, { useMemo, useState, useEffect, useRef } from "react";
import { Link, useSearchParams } from "react-router-dom";

const BASE = import.meta.env.BASE_URL;
const srcPath = (name) => `${BASE}images/${name}`;

// Categor√≠as principales
const CATEGORIAS = [
  { id: "todas", label: "Todas" },
  { id: "parking", label: "Sistema de Estacionamiento" },
  { id: "acceso", label: "Control de Acceso" },
  { id: "marcacion", label: "Sistema de Marcaci√≥n" },
  { id: "cctv", label: "CCTV" },
  { id: "incendio", label: "Alarma de Incendio" },
  { id: "robo", label: "Alarma de Robo" },
  { id: "domotica", label: "Automatizaci√≥n / Casa Inteligente" },
];

// Informaci√≥n por categor√≠a
const CATEGORIA_INFO = {
  parking: {
    titulo: "Parking que cobra solo, fluye solo y reporta todo",
    texto:
      "Optimiza entradas y salidas, elimina filas y controla la caja al centavo. Barreras, tiqueteras y software en una sola plataforma.",
    subcats: ["Software", "Barreras", "Tiqueteras", "Cobro"],
  },
  acceso: {
    titulo: "Acceso inteligente: r√°pido, seguro y auditable",
    texto:
      "Biometr√≠a, credenciales y m√≥vil en una misma soluci√≥n. Perfiles por horario, visitantes y trazabilidad total.",
    subcats: ["Lectoras", "Controladoras", "Integraciones"],
  },
  marcacion: {
    titulo: "Marcaci√≥n exacta, n√≥mina sin dolores",
    texto:
      "Biom√©trico, QR y app con ubicaci√≥n. Turnos, retardos y reportes listos para exportar a n√≥mina.",
    subcats: ["Biom√©trico", "QR", "Reportes"],
  },
  cctv: {
    titulo: "CCTV 4K con anal√≠tica que ve y entiende",
    texto:
      "Vigilancia IP 4K, acceso m√≥vil y anal√≠tica de video para detectar lo importante y actuar a tiempo.",
    subcats: ["IP 4K", "App M√≥vil", "Perimetral", "Integraci√≥n"],
  },
  incendio: {
    titulo: "Incendio direccionable con cumplimiento NFPA",
    texto:
      "Dise√±o, memoria de c√°lculo y pruebas. Detectores, estrobos y sirenas con respaldo documental.",
    subcats: ["Direccionable", "Dispositivos", "Pruebas"],
  },
  robo: {
    titulo: "Alarma de robo que disuade y notifica",
    texto:
      "Zonas y particiones con sensores perimetrales e interiores. App, automatizaciones y reporte a central.",
    subcats: ["Sensores", "Per√≠metro", "Particiones"],
  },
  domotica: {
    titulo: "Casa inteligente: escenas, confort y ahorro",
    texto:
      "Controla luces, clima y cerraduras desde app o voz. Escenas que elevan la experiencia y reducen consumo.",
    subcats: ["Escenas", "Cerraduras", "Control Total"],
  },
};

// Datos
const items = [
  { id:"parking-sw-01", titulo:"Software de manejo de parking", categorias:["parking"], subcat:"Software", fecha:"2024-06-10", ubicacion:"Panam√°", src: srcPath("Sistema_de_parking.jpg") },
  { id:"parking-bar-01", titulo:"Barreras de control vehicular", categorias:["parking"], subcat:"Barreras", fecha:"2024-06-10", ubicacion:"Panam√°", src: srcPath("Barreras.png") },
  { id:"parking-tiq-01", titulo:"Tiqueteras y receptoras", categorias:["parking"], subcat:"Tiqueteras", fecha:"2024-06-10", ubicacion:"Panam√°", src: srcPath("Tiquetera.png") },
  { id:"parking-cob-01", titulo:"Estaci√≥n de cobro", categorias:["parking"], subcat:"Cobro", fecha:"2024-06-10", ubicacion:"Panam√°", src: srcPath("Estacion_de_cobro.png") },
  { id:"acceso-lect-03", titulo:"Tipos de lectoras", categorias:["acceso"], subcat:"Lectoras", fecha:"2024-03-02", ubicacion:"Panam√°", src: srcPath("Control_de_acceso.png") },
  { id:"acceso-con-04", titulo:"Control con controladora y software", categorias:["acceso"], subcat:"Controladoras", fecha:"2024-03-02", ubicacion:"Panam√°", src: srcPath("Integracion_de_control_de_acceso.png") },
  { id:"marc-01", titulo:"Reloj biom√©trico con app", categorias:["marcacion"], subcat:"Biom√©trico", fecha:"2024-05-20", ubicacion:"Panam√°", src: srcPath("marcacion_biometrico_app.png") },
  { id:"marc-02", titulo:"Marcaci√≥n QR para visitantes", categorias:["marcacion"], subcat:"QR", fecha:"2024-06-02", ubicacion:"Costa del Este", src: srcPath("marcacion_qr.png") },
  { id:"marc-03", titulo:"Integraci√≥n n√≥mina y reportes", categorias:["marcacion"], subcat:"Reportes", fecha:"2024-03-28", ubicacion:"Clayton", src: srcPath("marcacion_reportes.png") },
  { id:"cctv-01", titulo:"Sistema CCTV IP 4K con NVR", categorias:["cctv"], subcat:"IP 4K", fecha:"2024-04-20", ubicacion:"Ciudad de Panam√°", src: srcPath("cctv_ip_4k.png") },
  { id:"cctv-02", titulo:"Monitoreo remoto con aplicaci√≥n m√≥vil", categorias:["cctv"], subcat:"App M√≥vil", fecha:"2024-05-10", ubicacion:"Costa del Este", src: srcPath("cctv_app_movil.png") },
  { id:"cctv-04", titulo:"Videovigilancia perimetral con anal√≠tica", categorias:["cctv"], subcat:"Perimetral", fecha:"2024-07-08", ubicacion:"Clayton", src: srcPath("cctv_perimetral_analitica.png") },
  { id:"cctv-06", titulo:"Integraci√≥n CCTV con control de acceso", categorias:["cctv"], subcat:"Integraci√≥n", fecha:"2024-08-15", ubicacion:"San Francisco", src: srcPath("cctv_integracion_acceso.png") },
];

function classNames(...c) {
  return c.filter(Boolean).join(" ");
}

export default function PortfolioGaleria() {
  const [searchParams, setSearchParams] = useSearchParams();
  const [cat, setCat] = useState(searchParams.get("cat") || "todas");
  const [sub, setSub] = useState(searchParams.get("sub") || "todas");
  const [q, setQ] = useState("");
  const [orden, setOrden] = useState("recientes");
  const [scrollY, setScrollY] = useState(0);
  const [currentIndex, setCurrentIndex] = useState(0);
  const scrollRef = useRef(null);

  useEffect(() => {
    const handleScroll = () => setScrollY(window.scrollY);
    window.addEventListener("scroll", handleScroll);
    return () => window.removeEventListener("scroll", handleScroll);
  }, []);

  useEffect(() => setSub("todas"), [cat]);

  useEffect(() => {
    const params = {};
    if (cat !== "todas") params.cat = cat;
    if (sub !== "todas") params.sub = sub;
    setSearchParams(params, { replace: true });
  }, [cat, sub, setSearchParams]);

  const data = useMemo(() => {
    let arr = [...items];
    if (cat !== "todas") arr = arr.filter((i) => i.categorias.includes(cat));
    if (sub !== "todas") arr = arr.filter((i) => (i.subcat || "").toLowerCase() === sub.toLowerCase());
    if (q.trim()) {
      const s = q.toLowerCase();
      arr = arr.filter((i) => i.titulo.toLowerCase().includes(s) || i.ubicacion.toLowerCase().includes(s));
    }
    arr.sort((a, b) => (orden === "recientes" ? b.fecha.localeCompare(a.fecha) : a.fecha.localeCompare(b.fecha)));
    return arr;
  }, [cat, sub, q, orden]);

  // === Rotaci√≥n autom√°tica y scroll ===
  useEffect(() => {
    if (window.innerWidth >= 768) return;
    const interval = setInterval(() => {
      setCurrentIndex((prev) => (prev + 1) % data.length);
    }, 30000);
    return () => clearInterval(interval);
  }, [data.length]);

  useEffect(() => {
    const container = scrollRef.current;
    if (container && window.innerWidth < 768) {
      const item = container.children[currentIndex];
      if (item) item.scrollIntoView({ behavior: "smooth", inline: "center" });
    }
  }, [currentIndex]);

  const info = CATEGORIA_INFO[cat];

  return (
    <div className="min-h-screen bg-white">
      {/* Cabecera */}
      <header
        className={`sticky top-0 z-30 transition-all duration-700 backdrop-blur ${
          scrollY > 10 ? "opacity-0 pointer-events-none" : "bg-white/70 border-b border-gray-200"
        }`}
      >
        <div className="max-w-7xl mx-auto px-4 py-4 flex flex-col gap-3 md:flex-row md:items-center md:justify-between">
          <h1 className="text-2xl md:text-3xl font-bold text-[#1A1A1A]">
            {cat === "todas" ? "Elige un servicio" : CATEGORIAS.find((c) => c.id === cat)?.label}
          </h1>
          <div className="flex flex-wrap gap-2">
            {CATEGORIAS.map((c) => (
              <button
                key={c.id}
                onClick={() => setCat(c.id)}
                className={classNames(
                  "px-3 py-1.5 rounded-full text-sm border transition-all",
                  cat === c.id
                    ? "bg-[#C1121F] text-white border-[#C1121F]"
                    : "bg-white text-[#2C3E50] hover:bg-gray-100 border-[#BDC3C7]"
                )}
              >
                {c.label}
              </button>
            ))}
          </div>
        </div>
      </header>

      {/* Subcategor√≠as */}
      {cat !== "todas" && info && (
        <section className="bg-[#0D3B66] text-white">
          <div className="max-w-7xl mx-auto px-4 py-6">
            <h2 className="text-2xl md:text-3xl font-extrabold">{info.titulo}</h2>
            <p className="mt-2 text-white/90">{info.texto}</p>
            <div className="mt-4 flex flex-wrap gap-2">
              {["todas", ...info.subcats].map((s) => (
                <button
                  key={s}
                  onClick={() => setSub(s)}
                  className={classNames(
                    "px-3 py-1.5 rounded-full text-sm border",
                    sub === s ? "bg-white text-[#0D3B66]" : "bg-transparent text-white border-white/50 hover:bg-[#0B3259]"
                  )}
                >
                  {s}
                </button>
              ))}
            </div>
          </div>
        </section>
      )}

      {/* Galer√≠a adaptable */}
      <main className="max-w-7xl mx-auto px-4 py-8">
        {data.length === 0 ? (
          <p className="text-center text-[#2C3E50]">Sin resultados.</p>
        ) : (
          <>
            <ul
              ref={scrollRef}
              className="flex md:grid overflow-x-auto md:overflow-visible md:grid-cols-3 snap-x snap-mandatory gap-6 scrollbar-hide scroll-smooth"
            >
              {data.map((it) => (
                <li
                  key={it.id}
                  className="flex-shrink-0 w-72 md:w-auto snap-center rounded-2xl overflow-hidden border border-gray-200 shadow-md bg-white hover:shadow-lg transition-all"
                >
                  <figure className="flex flex-col h-full">
                    <img src={it.src} alt={it.titulo} className="w-full h-56 object-cover" loading="lazy" />
                    <figcaption className="p-4 flex flex-col gap-2 flex-grow">
                      <h3 className="font-semibold text-[#1A1A1A] text-base md:text-lg">{it.titulo}</h3>
                      <p className="text-xs text-[#2C3E50] opacity-80">{it.subcat} ‚Ä¢ {it.ubicacion}</p>
                      <div className="mt-auto">
                        <Link
                          to={`/cotizador?mensaje=${encodeURIComponent(`Hola, estoy interesado en: ${it.titulo}`)}`}
                          className="px-3 py-1.5 text-xs font-semibold rounded-full bg-[#C1121F] text-white hover:bg-[#A10E1A]"
                        >
                          Cotizar
                        </Link>
                      </div>
                    </figcaption>
                  </figure>
                </li>
              ))}
            </ul>

            {/* Indicadores */}
            <div className="flex justify-center mt-6 gap-2 md:hidden">
              {data.map((_, i) => (
                <div
                  key={i}
                  className={`w-2.5 h-2.5 rounded-full transition ${
                    i === currentIndex ? "bg-[#C1121F]" : "bg-gray-300"
                  }`}
                />
              ))}
            </div>
          </>
        )}
      </main>
    </div>
  );
}
