import { useState, useCallback, useEffect } from "react";
import { motion, AnimatePresence } from "framer-motion";
import { Link } from "react-router-dom";

const BASE = import.meta.env.BASE_URL;

const proyectos = [
  { id: 1, titulo: "Remodelaci√≥n de local para restaurante", tipo: "imagen", src: `${BASE}images/obra1.jpg`, ubicacion: "P.H. Kamelia, V√≠a Espa√±a, Panam√°" },
  { id: 2, titulo: "Adecuaci√≥n de ba√±o", tipo: "imagen", src: `${BASE}images/obra2.jpg`, ubicacion: "P.H. Kamelia, V√≠a Espa√±a, Panam√°" },
  { id: 3, titulo: "Acondicionamiento para restaurante", tipo: "imagen", src: `${BASE}images/obra3.jpg`, ubicacion: "P.H. Kamelia, V√≠a Espa√±a, Panam√°" },
  { id: 4, titulo: "Port√≥n corredizo", tipo: "imagen", src: `${BASE}images/obra4.jpeg`, ubicacion: "Cuatro Altos, Col√≥n" },
  { id: 5, titulo: "Port√≥n y verjas en residencia", tipo: "imagen", src: `${BASE}images/obra5.jpg`, ubicacion: "Villa Lucre, Panam√°" },
  { id: 6, titulo: "Construcci√≥n de piscina", tipo: "imagen", src: `${BASE}images/obra6.jpg`, ubicacion: "Capira, Panam√° Oeste" },
  { id: 7, titulo: "Verjas con dise√±o", tipo: "imagen", src: `${BASE}images/obra7.jpg`, ubicacion: "Burunga, Panam√° Oeste" },
  { id: 8, titulo: "La mejor expericia en estructura", tipo: "video", src: `${BASE}videos/obra8.mp4`, ubicacion: "Villa Las Fuentes, Panam√°" },
  { id: 9, titulo: "Siempre con la mejor efisiciencia", tipo: "imagen", src: `${BASE}images/obra9.jpg`, ubicacion: "San Miguelito, Panam√°" },
  { id: 10, titulo: "Dise√±o en interior de sala", tipo: "video", src: `${BASE}videos/obra10.mp4`, ubicacion: "Urbanizaci√≥n El Bosque, Panam√°" },
];

export default function Construccion() {
  const [openIndex, setOpenIndex] = useState(-1);

  const close = useCallback(() => setOpenIndex(-1), []);
  const next = useCallback(() => setOpenIndex((i) => (i + 1) % proyectos.length), []);
  const prev = useCallback(() => setOpenIndex((i) => (i - 1 + proyectos.length) % proyectos.length), []);

  // Control de teclado
  useEffect(() => {
    const onKey = (e) => {
      if (e.key === "Escape") close();
      if (e.key === "ArrowRight") next();
      if (e.key === "ArrowLeft") prev();
    };
    window.addEventListener("keydown", onKey);
    return () => window.removeEventListener("keydown", onKey);
  }, [close, next, prev]);

  return (
    <section className="min-h-screen text-white relative">
      {/* === Encabezado === */}
      <div
        className="relative flex flex-col items-center justify-center min-h-[70vh] text-center"
        style={{
          backgroundImage: `url(${BASE}images/fondo_construccion.jpg)`,
          backgroundSize: "cover",
          backgroundPosition: "center",
        }}
      >
        <div className="absolute inset-0 bg-black/70"></div>

        <div className="relative z-10 max-w-4xl px-6">
          <h1 className="text-5xl font-extrabold text-[#FFD700] mb-6 animate-fadeInUp">
            Divisi√≥n de Construcci√≥n
          </h1>
          <p className="text-lg md:text-xl mb-8 text-gray-200 leading-relaxed">
            TCT Services ofrece soluciones completas en construcci√≥n: desde obra gris y remodelaciones
            hasta sistemas el√©ctricos, acabados y proyectos llave en mano.
            <br />
            <br />
            Nuestro compromiso es la excelencia, seguridad y puntualidad en cada obra.
          </p>

          <Link
            to="/cotizador?mensaje=Estoy interesado en los servicios de construcci√≥n de TCT Services"
            className="inline-block px-8 py-3 bg-[#C1121F] hover:bg-[#A10E1A] rounded-2xl font-semibold shadow-lg"
          >
            Solicitar Cotizaci√≥n
          </Link>
        </div>
      </div>

      {/* === Galer√≠a === */}
      <motion.div
        initial={{ opacity: 0, y: 30 }}
        animate={{ opacity: 1, y: 0 }}
        transition={{ duration: 0.8 }}
        className="bg-white text-[#1A1A1A] py-16 px-6"
      >
        <h2 className="text-3xl font-bold text-center mb-10 text-[#0D3B66]">
          Proyectos Realizados
        </h2>

        <div className="max-w-6xl mx-auto grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 gap-6">
          {proyectos.map((obra, idx) => (
            <div
              key={obra.id}
              className="group relative overflow-hidden rounded-2xl shadow-md border border-gray-200 hover:shadow-xl transition cursor-pointer"
              onClick={() => setOpenIndex(idx)}
            >
              {obra.tipo === "video" ? (
                obra.src.includes("youtube") ? (
                  <iframe
                    src={obra.src}
                    title={obra.titulo}
                    className="w-full h-64 object-cover"
                    allow="autoplay; encrypted-media"
                  ></iframe>
                ) : (
                  <video
                    src={obra.src}
                    muted
                    loop
                    autoPlay
                    playsInline
                    className="w-full h-64 object-cover group-hover:scale-105 transition-transform duration-500"
                  />
                )
              ) : (
                <img
                  src={obra.src}
                  alt={obra.titulo}
                  className="w-full h-64 object-cover group-hover:scale-105 transition-transform duration-500"
                  loading="lazy"
                />
              )}
              <div className="absolute inset-0 bg-black/50 opacity-0 group-hover:opacity-100 transition flex flex-col justify-center items-center text-white p-4 text-center">
                <h3 className="text-lg font-semibold">{obra.titulo}</h3>
                <p className="text-sm text-gray-200 mt-1">{obra.ubicacion}</p>
              </div>
            </div>
          ))}
        </div>
      </motion.div>

      {/* === Lightbox === */}
      <AnimatePresence>
        {openIndex >= 0 && (
          <motion.div
            key="lightbox"
            initial={{ opacity: 0 }}
            animate={{ opacity: 1 }}
            exit={{ opacity: 0 }}
            className="fixed inset-0 z-50 bg-black/90 flex items-center justify-center p-4"
            onClick={close}
          >
            <motion.div
              initial={{ scale: 0.95 }}
              animate={{ scale: 1 }}
              exit={{ scale: 0.9 }}
              transition={{ duration: 0.3 }}
              className="relative max-w-5xl w-full"
              onClick={(e) => e.stopPropagation()}
            >
              {proyectos[openIndex].tipo === "video" ? (
                proyectos[openIndex].src.includes("youtube") ? (
                  <iframe
                    src={proyectos[openIndex].src}
                    className="w-full h-[70vh] rounded-xl"
                    allowFullScreen
                  ></iframe>
                ) : (
                  <video
                    src={proyectos[openIndex].src}
                    controls
                    autoPlay
                    className="w-full h-auto rounded-xl"
                  />
                )
              ) : (
                <img
                  src={proyectos[openIndex].src}
                  alt={proyectos[openIndex].titulo}
                  className="w-full h-auto rounded-xl"
                />
              )}
              <div className="text-white mt-3 flex justify-between items-center">
                <div>
                  <h3 className="text-xl font-semibold">{proyectos[openIndex].titulo}</h3>
                  <p className="text-sm text-gray-300">{proyectos[openIndex].ubicacion}</p>
                </div>
                <div className="flex gap-2">
                  <button onClick={prev} className="px-3 py-2 bg-white/10 hover:bg-white/20 rounded">‚Üê</button>
                  <button onClick={next} className="px-3 py-2 bg-white/10 hover:bg-white/20 rounded">‚Üí</button>
                  <button onClick={close} className="px-3 py-2 bg-white/10 hover:bg-white/20 rounded">‚úï</button>
                </div>
              </div>
            </motion.div>
          </motion.div>
        )}
      </AnimatePresence>
    </section>
  );
}
